@keyframes animationCard {
    from {
        opacity: 0.4;
        width: 30%;
        transform: rotateZ(-90deg);
    }
    to {
        transform: rotate(0);
        opacity: 1;
        width: 100%;
    }
}

.main {
    background-color: #f6f7f9;
    min-height: 100vh;
    position: relative;
    header {
        background-image: linear-gradient(to bottom right, #0e5449, #446d67);
        height: 200px;
        display: flex;
    }
    .wrapCard {
        -webkit-box-shadow: 2px 0px 16px -3px rgba(0, 0, 0, 0.75);
        -moz-box-shadow: 2px 0px 16px -3px rgba(0, 0, 0, 0.75);
        box-shadow: 2px 0px 16px -3px rgba(0, 0, 0, 0.75);
        margin-top: 20px;
        .CardCov {
            animation-name: animationCard;
            animation-duration: 2s;
        }
    }
}

.ant-input {
    color: #000;
    font-size: 18px;
}
